import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useSearchParams } from 'react-router-dom'

import {
  CircularProgress,
  Container,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Pagination,
  Select,
  type SelectChangeEvent,
  Stack,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import orders from 'src/data/SortingOrders'
import CATEGORIES from 'src/data/categories'
import departments from 'src/data/departments'
import { CLUB_TYPES, type ClubType } from 'src/data/types'

import ClubList from 'src/components/ClubList'
import { type ClubPreviewProps } from 'src/components/ClubPreview'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import Searchbox from 'src/components/Searchbox'

interface SearchAPIResponse {
  clubs: ClubPreviewProps[]
  numPages: number
}

const SELECT_COMPONENT_WIDTH = '160px'

function Search() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const [searchParams, setSearchParams] = useSearchParams()
  const [params, setParams] = useState({
    name: searchParams.get('name') ?? '',
    category: searchParams.get('category') ?? '',
    type: searchParams.get('type') ?? '',
    page: searchParams.has('page') ? parseInt(searchParams.get('page') || '1') : null,
    order: searchParams.has('order') ? searchParams.get('order') : ''
  })
  const [clubs, setClubs] = useState<ClubPreviewProps[]>([])
  const [numPages, setNumPages] = useState(1)
  const [isLoading, setIsLoading] = useState(false)
  const { enqueueSnackbar } = useSnackbar()
  const isDepartmentClubSearchPage = params.type === 'DEPARTMENT_CLUB'

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = event => {
    event.preventDefault()
    const search = document.getElementById('search') as HTMLInputElement
    setParams(prev => ({ ...prev, name: search.value, page: null, order: '' }))
  }

  const handleChange = ({
    target: { name, value }
  }: React.ChangeEvent<HTMLInputElement> | SelectChangeEvent<string>) => {
    setParams(prev => ({ ...prev, category: name === 'type' ? '' : prev.category, page: null, [name]: value }))
  }

  const handlePageChange = (event: React.ChangeEvent<HTMLInputElement | unknown>, value: number) =>
    setParams(prev => ({ ...prev, page: value === 1 ? null : value }))

  useEffect(() => {
    const newParams = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.type ? { type: params.type } : null,
      params.page ? { page: params.page } : null,
      params.order ? { order: params.order } : null
    )
    setSearchParams(newParams)
  }, [params])

  useEffect(() => {
    setIsLoading(true)
    const apiParams = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.type ? { type: params.type } : null,
      params.page ? { page: params.page } : null,
      { count: 12 },
      params.order ? { order: params.order } : null
    )
    axios
      .get<SearchAPIResponse>(`${process.env.REACT_APP_APIHOST}/search/clubs`, {
        params: apiParams
      })
      .then(res => {
        setNumPages(res.data?.numPages)
        setClubs(res.data?.clubs ?? [])
        setIsLoading(false)
      })
      .catch(err => {
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
        setIsLoading(false)
      })
  }, [searchParams])

  return (
    <>
      <Helmet>
        <title>검색 결과</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container alignItems="center">
          <Grid item>
            <Stack direction="row" alignItems="baseline">
              <Typography variant="h3" component="h3" gutterBottom sx={{ marginRight: 1 }}>
                {params.type === '' ? '검색 결과' : CLUB_TYPES[params.type as ClubType].name}
              </Typography>
              <Typography variant="inherit" gutterBottom>
                {params.category}
              </Typography>
            </Stack>
            <Typography variant="subtitle2">키워드와 카테고리, 분류를 활용해 원하는 동아리를 찾아보세요</Typography>
          </Grid>
        </Grid>
        <Searchbox handleSubmit={handleSubmit} defaultValue={params.name} />
      </PageTitleWrapper>
      <Container
        sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', alignItems: 'center' }}
      >
        <Stack
          width="100%"
          direction={matches ? 'row' : 'column'}
          spacing={1}
          justifyContent={'flex-end'}
          sx={{ marginBottom: 3 }}
        >
          {isDepartmentClubSearchPage ? (
            <FormControl size="small" fullWidth={!matches}>
              <InputLabel id="department-label">단과대</InputLabel>
              <Select
                labelId="department-label"
                id="department"
                name="category"
                label="단과대"
                value={params.category}
                onChange={handleChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {Object.keys(departments)?.map(department => (
                  <MenuItem key={department} value={department}>
                    {department}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          ) : (
            <FormControl size="small" fullWidth={!matches}>
              <InputLabel id="category-label">카테고리</InputLabel>
              <Select
                labelId="category-label"
                id="category"
                name="category"
                label="카테고리"
                value={params.category}
                onChange={handleChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {CATEGORIES?.map(category => (
                  <MenuItem key={category} value={category}>
                    {category}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          )}

          <FormControl size="small" fullWidth={!matches}>
            <InputLabel id="type-label">분류</InputLabel>
            <Select
              labelId="type-label"
              id="type"
              name="type"
              label="분류"
              value={params.type}
              onChange={handleChange}
              sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
            >
              <MenuItem value="">선택하지 않음</MenuItem>
              {CLUB_TYPES &&
                Object.entries(CLUB_TYPES).map(([key, { name }]) => (
                  <MenuItem key={key} value={key}>
                    {name}
                  </MenuItem>
                ))}
            </Select>
          </FormControl>
          <FormControl size="small" fullWidth={!matches}>
            <InputLabel id="order-label">정렬</InputLabel>
            <Select
              labelId="order-label"
              id="order"
              name="order"
              label="정렬"
              value={params.order ?? ''}
              onChange={handleChange}
              sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
            >
              <MenuItem value="">선택하지 않음</MenuItem>
              {orders &&
                Object.entries(orders).map(([key, value]) => (
                  <MenuItem key={key} value={key}>
                    {value}
                  </MenuItem>
                ))}
            </Select>
          </FormControl>
        </Stack>
        {isLoading ? <CircularProgress size={64} disableShrink thickness={3} /> : <ClubList clubs={clubs} />}
        {clubs.length !== 0 && (
          <Pagination
            page={params.page ?? 1}
            count={numPages ?? 1}
            size={matches ? 'medium' : 'small'}
            onChange={handlePageChange}
            sx={{ my: 3 }}
          />
        )}
      </Container>
    </>
  )
}

export default Search
